.hero {
  background-image: url("../img/hero/hero-background.jpg"),
    linear-gradient(to bottom, $bg-branded 63%, $bg-basic-light 63%);
  background-position: top;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    background-image: none;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/hero/hero-background-desktop.jpg"),
      linear-gradient(to left, $bg-branded 50%, $bg-basic-light 50%);
    background-repeat: no-repeat;
    background-position: right;
    margin-top: -125px;
    z-index: -1;
    position: relative;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero/hero-background-desktop@2x.jpg"),
        linear-gradient(to left, $bg-branded 50%, $bg-basic-light 50%);
      background-size: contain;
    }
  }
}


