.hero__container {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 27px 20px 0 20px;
  text-align: center;
  box-sizing: border-box;
  position: relative;


  @media (min-width: $tablet-width) {
    background: $bg-basic-light;
    width: $tablet-width;
    padding: 62px 30px 0;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    min-height: 694px;
    background: transparent;
    padding: 230px 110px 0 110px;
    margin-bottom: 88px;
  }
}


