.hero__title {
  margin: 0;
  color: $text-basic-light;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom: 25px;
  font-family: $font-oswald;
  text-align: center;

  @media (min-width: $tablet-width) {
    color: $text-basic-dark;
    font-size: 60px;
    font-weight: 400;
    line-height: 60px;
    text-align: left;
    max-width: 450px;
    padding-left: 90px;
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    margin-left: 80px;
  }
}

.hero__description {
  margin: 0;
  font-family: $font-oswald;
  color: $text-basic-light;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    color: $text-basic-dark;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    text-align: left;
    padding-left: 90px;
    margin-bottom: 21px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    margin-left: 80px;
  }
}

.hero__image {
  margin: 0 auto;
  display: block;
  margin-bottom: 3px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 149px;
    left: 559px;
  }
}

.hero__link {
  min-height: 41px;
  width: 280px;
  align-items: center;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 290px;
    left: 120px;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    padding: 14px 26px;
    width: 244px;
    min-height: 54px;
  }

  @media (min-width: $desktop-width) {
    top: 463px;
    left: 190px;
  }
}
